@mixin projects {
  .projects {
    .project-container {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    .project {
      margin: auto;
      display: flex;
      flex-direction: column;
      color: white;
      height: 100%;
      width: 100%;
      padding: 20px;
      box-shadow: 1px 1px 5px rgb(0, 0, 0);
      margin-bottom: 10px;
    }
    .project_img {
      width: 100%;
      img {
        width: 90%;
      }
      p {
        margin-bottom: 20px;
        text-align: center;
        font-family: $secondaryFont, sans-serif;
      }
      a {
        text-align: center;
        text-decoration: none;
        color: white;
        font-family: $secondaryFont, sans-serif;
        // div{
        //     font-family: $secondaryFont, sans-serif;
        // }
      }
      .buttons {
        display: flex;
        width: 100%;
        justify-content: space-between;
        div {
          border: 1px solid $grey;
          padding: 10px;
          border-radius: 20px;
          transition: 0.5s;
          &:hover {
            border: 1px solid $yellow;
            color: $yellow;
          }
        }
      }
      .seeMore {
        text-align: center;
        width: 100%;
        cursor: pointer;
      }
    }
    .project_details_active {
      display: block !important;
    }
    .project_details {
      width: 100%;
      display: none;
      h1 {
        font-family: $primaryFont, sans-serif;
      }
      h3,
      p {
        font-family: $secondaryFont, sans-serif;
      }
    }
  }
}
