@mixin contacts {
  .contact {
    padding-bottom: 50px;
    h2 {
      margin-top: -50px;
      margin-bottom: 20px;
      text-align: center;
    }
    .contact-container {
      display: flex;
      flex-direction: column;
      width: 90%;
      margin: auto;
      justify-content: space-between;
      ul {
        list-style-type: none;
      }
      li {
        display: flex;
      }
      p {
        margin-left: 10px;
      }
      div {
        margin: auto;
      }
      h1 {
        text-align: center;
      }
    }
    .socialDetails {
      margin-top: 30px !important;
      ul {
        display: flex;
      }
      li {
        margin: 10px;
        &:hover {
          color: $yellow;
        }
      }
      a {
        text-decoration: none;
        color: white;
      }
    }
  }
}
